.crear-nuevo-todo{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    background-color: #09d4ff;
    margin: 10px 0;
    border-radius: 20px;
    border: 2px solid #7BDFF2;
    height: 50px;
    width: 100%;
    justify-content: center;
    cursor: pointer;
}

.text-nuevo-todo{
    color: #001242;
    font-family: 'Roboto Slab', serif;
    font-weight: bold;
    font-size: 1.1rem;
}

.crear-nuevo-todo:hover{
    opacity: 0.9;
}

